.card{
    width: 30%;
    height: 400px;
    margin: 10px;
    background-color: white;
    border-radius: 6px;
    box-sizing: border-box;
    box-shadow: 0px 0px 15px #ccc;
    cursor: pointer;
}

.card img{
    width: 100%;
    height: 180px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}

.content{
    padding: 15px;
}

.content h2{
    font-size: 20px;
    margin: 5px 0px;
}

.content .name{
    color: #52c3ff;
}

.content p{
    font-weight: 400;
    color: #c4c4c4;
}