.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 80px;
    background-color: white;
    box-sizing: border-box;
    box-shadow: 0px 1px 2px #ccc;
    position: sticky;
    top: 0;
    /* padding: 10px; */
}

.logo h1{
    cursor: pointer;
    font-size: 33px;
    font-weight: 800;
    margin-bottom: 0px;
    padding-bottom: 0px;
    -webkit-background-clip:text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(200deg,#0048ff,#0096ff);
    line-height: 1;
    -webkit-font-smoothing: antialiased
    
}
.logo p{
    margin: 0px;
    padding: 0px 8px;
    font-size: 16px;
    line-height: 1;
    font-style: italic;
    letter-spacing: 2;
    font-weight: 400;
}

.left{
    display: flex;
    align-items: center;
}
.left ul li{
    list-style: none;
    cursor: pointer;
}

.left h1,p{
    margin: 5px;
}

.right{
    display: flex;
    align-items: center;
}

.right p{
    padding: 0 20px; 
    width: 340px;
    font-weight: 500;
    font-size: 15px;
    color: #c4c4c4;
    /* box-sizing: border-box; */
}

.right button{
    background: linear-gradient(200deg,#0048ff,#0096ff);
    border: none;
    color: white;
    cursor: pointer;
    border-radius: 25px;
    height: fit-content;
    padding: 15px;
    font-size: 12px;

}