.data_container{
    /* border: 2px solid red; */
    width:70% ;
    margin: auto;
    padding: 10px 20px;
    box-sizing: border-box;
}

.data{
    display: inline-block;
    margin: 6px;
    border: 1px solid #52c3ff;
    padding: 7px;
    color:#52c3ff ;
    border-radius: 15px;
    font-size: 13px;
    cursor: pointer;
    /* background: linear-gradient(181deg,#0048ff,#0096ff); */
}

.active{
    background: linear-gradient(200deg,#0048ff,#0096ff);
    color: white;
    display: inline-block;
    margin: 6px;
    border: 1px solid #52c3ff;
    padding: 8px;
    border-radius: 15px;
    font-size: 13px;
    cursor: pointer;
}

.data:hover{
    background: linear-gradient(200deg,#0048ff,#0096ff);
    color: white;
}

